.primaryText{
  @media (max-width: $sm) {
    font-size: 2.5rem;
  }
  font-size: 3rem;
  font-weight: bold;
  color: #0d2f3f
}

.secondaryText {
  @media (max-width: $sm) {
    font-size: 1.1rem;
    line-height: 30px;
  }
  line-height: 40px;
  font-size: 1.3rem;
  font-weight: 500;
  color : #0d2f3f;
}
.anchor {
  display: block;
  position: relative;
  top: -100px;
  visibility: hidden;
}